// src/app/auth/layout.tsx
import type { ReactNode } from 'react';

import styles from './AuthLayout.module.css';

export default function AuthLayout({ children }: { children: ReactNode }) {
  return (
    <div className={styles.authContainer}>
      <section className={styles.authBox}>
        <h1 className={`titulo-bi ${styles.title}`}>Bienvenida a Process Girl</h1>
        <h2 className={`texto-dec ${styles.subtitle}`}>✨tu espacio para crecer, organizarte y brillar✨</h2>
        <p className={styles.cta}>Accede o crea tu cuenta para continuar</p>
        {children}
      </section>
    </div>
  );
}
